* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: "Raleway", sans-serif;
  background-image: url(/portfolio/static/media/home.eb36817d.jpg);
  background-repeat: no-repeat;
  background-size: 91vw 100vh;
  /* overflow: hidden; */
}

@-webkit-keyframes slideup {
  from {
    top: 300px;
    opacity: 0;
  }
  to {
    top: 205px;
    opacity: 1;
  }
}

@keyframes slideup {
  from {
    top: 300px;
    opacity: 0;
  }
  to {
    top: 205px;
    opacity: 1;
  }
}

.view {
  width: 3vw;
  float: right;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  overflow: hidden;
}

.view:hover {
  width: 91vw;
  -webkit-animation-name: expand;
          animation-name: expand;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

@-webkit-keyframes expand {
  from {
    width: 3vw;
  }
  to {
    width: 91vw;
  }
}

@keyframes expand {
  from {
    width: 3vw;
  }
  to {
    width: 91vw;
  }
}

